@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* popups */
// inline popup
.inline-popup {
	max-width: $popup-width;
	padding: $popup-space;
	background-color: $bg-color-base;
	@include push-auto; }

// default popup
.default-popup {
	max-width: $default-popup-width;
	padding: $default-popup-space;
	background-color: $bg-color-alt;
	@include push-auto;

	@include media('<tab-sm') {
		max-width: none;
		min-height: 100vh;
		padding: #{30+$header-height--lg} #{$layout-spaces--sm} #{$layout-spaces--sm}; }

	@include media('<mob-lg') {
		padding: #{30+$header-height--lg} #{$layout-spaces--mob} #{$layout-spaces--mob}; } }

// popup title
.inline-popup__title, .default-popup__title {
	@include font-alt-bold;
	@include font(24px, round-float(32/24, 2));
	margin-bottom: $bottom-space--md;
	padding-right: 20px;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

// pseudotabs-nav
.default-popup, .inline-popup {
	.pseudotabs-nav {
		padding-right: 40px;
		margin-bottom: 20px;

		@include media('<tab-sm') {
			padding-right: 0; } }

	.pseudotabs-tab {
		margin-bottom: 20px; } }

/* popups end */
