@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* magnific popup */
.mfp-bg {
	background-color: #3e3e3d; }

.mfp-container {
	max-width: $page-layout-width;

	@include media('<desk-hd') {
		max-width: 100%; }

	@include media('<tab-sm') {
		padding: 0;

		&::before {
			content: none; } } }

//.mfp-content
//include media('<tab-sm')
//height: 100%

// popup button close
.mfp-close {
	$mfp-close-btn-size: 40px;
	$mfp-close-btn-size--mob: 60px;
	$mfp-close-icon-size: 12px;
	$mfp-close-icon-size--mob: 16px;

	@include size($mfp-close-btn-size);
	opacity: 1;

	@include media('<tab-sm') {
		@include size($mfp-close-btn-size--mob); }

	svg {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size($mfp-close-icon-size);
		margin: -#{round($mfp-close-icon-size/2)} 0 0 -#{round($mfp-close-icon-size/2)};
		z-index: -1;
		pointer-events: none;

		@include media('<tab-sm') {
			@include size($mfp-close-icon-size--mob);
			margin: -#{round($mfp-close-icon-size--mob/2)} 0 0 -#{round($mfp-close-icon-size--mob/2)}; } }

	path {
		fill: inherit; }

	.mfp-close-btn-in & {
		$space: round($popup-space - (($mfp-close-btn-size - $mfp-close-icon-size)/2));

		fill: $text-color;
		color: $text-color;
		top: $space;
		right: $space;

		@include media('<tab-sm') {
			top: 0;
			right: auto;
			left: 0;
			fill: white;
			color: white;
			background-color: $btn-bg-color; }

		.no-touchevents &:hover {
			fill: $link-color;
			color: $link-color; } } }

/*
 *
 *====== Zoom effect ====== */
.mfp-zoom-in {
	/* start state */
	.mfp-with-anim {
		opacity: 0;
		transition: all 0.2s ease-in-out;
		transform: scale(0.8);

		// для девайсов отключаем эффект всплывание
		@include media('<tab-sm') {
			transform: none; } }

	&.mfp-bg {
		opacity: 0;
		transition: all 0.3s ease-out; }

	/* animate in */
	&.mfp-ready {
		.mfp-with-anim {
			opacity: 1;
			transform: scale(1); }

		&.mfp-bg {
			opacity: 0.5; } }

	/* animate out */
	&.mfp-removing {
		.mfp-with-anim {
			transform: scale(0.8);
			opacity: 0;

			@include media('<tab-sm') {
				transform: none; } }

		&.mfp-bg {
			opacity: 0; } } }

/* magnific popup end */
