/* options panel */
.p-options {
	position: relative;
	margin-bottom: 6rem;

	@include media('<desk-sm') {
		margin-bottom: 4rem; }

	@include media('<tab-lg') {
		border: 1px solid $border-color; }

	@include media('<mob-lg') {
		margin-bottom: 3rem; }

	&:last-child {
		margin-bottom: 0; } }

.p-options__list {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	flex-wrap: nowrap;

	//.no-flexbox &
	//isplay: table
	//idth: 100%
	//able-layout: fixed

	@include media('<mob-md') {
		//display: block
		position: relative;
		padding-right: 60px;
		min-height: 80px; } }

.p-options__item {
	@include font-d;
	font-style: normal;
	text-align: center;

	@include media('<tab-lg') {
		font-size: 12px; }

	&:first-child {
		text-align: left;
		flex: 1 1;

		@include media('<tab-lg') {
			padding: round($grid-space/2) $grid-space; }

		@include media('<mob-lg') {
			padding: round($grid-space--md/2) $grid-space--md; } }

	&:last-child {
		text-align: right; }

	.no-flexbox & {
		display: table-cell;
		vertical-align: middle; }

	.view-switcher {
		@include display-inline-block-top;

		@include media('<tab-lg') {
			display: block;
			height: 100%; } }

	&.p-options__item--filters-show,
	&.p-options__item--view-switcher {
		@include media('<tab-lg') {
			@include size(60px);
			border: 1px solid $border-color;
			margin: -1px -1px -1px 0; }

		@include media('<mob-md') {
			position: absolute;
			right: -1px;
			bottom: -1px;
			margin: 0;
			height: calc(50% + 2px); } }

	&.p-options__item--filters-show {
		display: none;

		@include media('<tab-lg') {
			display: block; }

		@include media('<mob-md') {
			top: -1px;
			height: calc(50% + 1px); } } }

.btn-filters-show {
	position: relative;
	@include display-inline-block-top;
	color: $text-color;
	user-select: none;

	@include media('<tab-lg') {
		display: block;
		height: 100%; }

	& > span {
		@include hide-text; }

	svg {
		@include display-inline-block-top;
		@include size(24px);

		@include media('<tab-lg') {
			position: absolute;
			left: 50%;
			top: 50%;
			margin: -12px 0 0 -12px; } }

	path {
		fill: currentColor; }

	// hover
	.no-touchevents &:hover {
		color: $base-color; } }


//.view-switcher-wrap
//include media('<tab-lg')
//display: none

.p-options__inner {
	position: relative;
	height: 100%; } // нужно, когда принимают форму квадрата на девайсах

.p-options__group {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	user-select: none;
	text-align: left;

	//+media('<tab-lg')
	//ustify-content: flex-end

	//+media('<tab-sm')
	//ustify-content: center

	.p-options__label {
		@include display-inline-block-middle;
		margin: 4px 25px 4px 0;
		@include font(inherit, inherit);
		color: $text-color-light;

		@include media('<desk-md') {
			margin-right: 15px; }

		@include media('<tab-sm') {
			display: none; } } }

.p-sorting {
	@include display-inline-block-middle;

	&, .#{$user-content} & {
		margin: 0;

		li {
			padding: 0;
			margin: 4px 18px 4px 0;

			@include media('<desk-md') {
				margin-right: 10px; }

			&::before {
				content: none; } } }

	//&.active
	//adding-right: 38px

	li {
		@include display-inline-block-top;
		white-space: nowrap;
		position: relative;
		cursor: pointer;

		// стрелочка развернута ВВЕРХ, сортировака ПО ВОЗРАСТАНИЮ
		.arr {
			position: absolute;
			top: 50%;
			right: 0;
			@include triangle(top, #{$text-color}, 4px, 2);
			margin-top: -4px;
			opacity: 0;
			transform: rotate(0) translateZ(0);
			transition: {
				property: transform, opacity;
				duration: $animation-speed; }

			.order-desc & {
				transform: rotate(0.5turn) translateZ(0); } } }

	a {
		display: block;
		position: relative;
		text-decoration: none;
		color: inherit;
		padding: 0 12px 0 0;

		// hover
		&:hover {
			color: $base-color; }

		& > span em {
			display: none;
			font-style: inherit;
			font-weight: inherit;
			font-family: inherit;

			@include media('<tab-sm') {
				display: inline; } } }

	// активное соотояние
	li.active {

		a {
			color: $base-color;

			& > span {
				border-bottom-color: transparent; }

			// стрелочка развернута ВНИЗ, сортировака ПО УБЫВАНИЮ
			.arr {
				opacity: 1;
				border-bottom-color: $base-color; } } } }

/* options panel end */

