/* product card */
.p-card {
	@include clear;
	//margin-bottom: $bottom-space--lg

	//+media('<desk-lg')
	//argin-bottom: $bottom-space--ml

	.breadcrumbs {
		@include media('<desk-md') {
			margin-bottom: $p-card-elem-bottom-space--md; } } }

// title
.p-card__title {
	margin-bottom: $p-card-elem-bottom-space;

	@include media('<desk-md') {
		margin-bottom: $p-card-elem-bottom-space--md; }

	h1, h2, h3 {
		@include font-alt-bold;
		@include font(#{$font-size-h2}, #{$line-height-h2});
		letter-spacing: 0.05em;
		margin-bottom: $p-card-elem-bottom-space;

		@include media('<desk-md') {
			@include font($font-size-h2--md, $line-height-h2--md);
			margin-bottom: $p-card-elem-bottom-space--md; }

		@include media('<mob-lg') {
			@include font($font-size-h2--sm, $line-height-h2--sm); }

		&:last-child {
			margin-bottom: 0; } }

	.to-catalog {
		text-align: right;
		margin-bottom: $bottom-space; } }

.p-card__state {
	@include font-d();

	.p-card__model, .p-card__store {
		@include display-inline-block-top; } }

.p-card__model {
	color: $text-color-light; }

.p-card__store {
	&::before {
		content: "/";
		display: none;
		color: $text-color-light; }

	.p-card__model + &::before {
		display: inline-block; }

	svg {
		@include display-inline-block-top;
		@include size(14px);
		margin-top: 3px; }

	path, polygon {
		fill: inherit; }

	&.on {
		$color: #32b39b;
		// ================
		color: $color;
		fill: $color; }

	&.off {
		$color: #ff203b;
		// ================
		color: $color;
		fill: $color;

		svg {
			@include size(10px);
			margin-top: 5px; } } }

// product photos
.p-card, .p-card__content__holder {
	padding: 0 $p-card-space;

	@include media('<desk-hd') {
		@include padding-horizontal($p-card-space--hd); }

	@include media('<desk-lg') {
		@include padding-horizontal($p-card-space--md); }

	@include media('<desk-sm') {
		@include padding-horizontal($p-card-space--sm); }

	@include media('<tab-lg') {
		@include padding-horizontal(0); } }

.p-card {
	@include media('<tab-lg') {
		@include padding-horizontal(0); } }

.p-card__visual {
	float: left;
	width: nth($p-card-ratio, 1);
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		width: nth($p-card-ratio--lg, 1);
		margin-bottom: $bottom-space--lg; }

	@include media('<tab-lg') {
		float: none;
		width: nth($p-card-ratio--sm, 1);
		margin-bottom: $bottom-space--md; }

	&:last-child {
		margin-bottom: 0; } }

.p-card__gallery__list {
	margin-bottom: $bottom-space--lg;

	@include media('<desk-hd') {
		margin-bottom: $bottom-space--md; }

	@include media('<tab-sm') {
		margin-bottom: $bottom-space--sm; }

	&:last-child {
		margin-bottom: 0; } }

.p-card__gallery__item {
	//height: calc(100vh - 264px) // костыль
	position: relative;
	height: 100vh;
	text-align: center;
	// для сжатия по высоте / плохо анимируется
	//+clear-spacing

	@include media('<tab-lg') {
		height: 560px; }

	@include media('<tab-sm') {
		height: 480px; }

	@include media('<mob-lg') {
		height: 320px; }

	@include media('<mob-md') {
		height: 240px; }

	// для сжатия по высоте / плохо анимируется
	//&::before
	//ontent: ""
	//isplay: inline-block
	//ertical-align: middle
	//include size(0, 100%)

	img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(none);
		//+object-fit(scale-down)
		@include object-fit(contain);
		visibility: hidden;

		@include media('<tab-lg') {
			visibility: visible; }

		.no-js & {
			width: auto;
			height: auto;
			max-width: 100%; } } }

.p-card__gallery__inner {
	position: relative;
	// для сжатия по высоте / плохо анимируется
	//+display-inline-block-middle
	@include size(100%, 100%);
	font-size: $font-size;
	letter-spacing: normal;
	background-position: center;
	background-repeat: no-repeat;
	background-size: auto 80%;

	@include media('<tab-lg') {
		background: none !important; } }

/* p-card__gallery */
.p-card__gallery__list {
	.swiper-wrapper {
		@include media('>=tab-lg') {
			// remove vendor styles
			transform: none !important;
			display: block;
			width: auto;
			height: auto;
			z-index: auto; } } }

.p-card__gallery__item {
	@include media('>=tab-lg') {
		width: auto !important; }

	&.swiper-slide-duplicate {
		@include media('>=tab-lg') {
			display: none; } } }

.swiper-lazy-preloader {
	@include media('>=tab-lg') {
		display: none; } }

.p-card__gallery__prev,
.p-card__gallery__next,
.p-card__gallery__pagination {
	user-select: none;

	@include media('>=tab-lg') {
		display: none; } }

.p-card__gallery__prev,
.p-card__gallery__next {
	top: auto;
	bottom: 2.5rem;
	margin-bottom: 3px;

	@include media('<mob-lg') {
		bottom: 0; }

	&::before, &::after {
		border-color: $text-color; } }

.p-card__gallery__prev {
	left: 0; }

.p-card__gallery__next {
	right: 0; }

.p-card__gallery__pagination {
	position: absolute;
	left: 50%;
	bottom: 2.5rem;
	width: auto;
	min-width: 85px;
	@include padding-horizontal(25px);
	transform: translate(-50%, 0);
	@include font-alt-italic;
	@include font(20px, 1);
	text-align: center;

	@include media('<mob-lg') {
		bottom: 0; } }

/* p-card__gallery end */

// widget
.p-card__widget {
	margin-top: $p-card-elem-bottom-space;
	@include font(#{$font-size}, #{$line-height});

	@include media('<desk-md') {
		margin-top: $p-card-elem-bottom-space--md; }

	&:first-child {
		margin-top: 0; } }

.p-card__widget__title {
	display: block;
	@include font-d();
	margin-bottom: $bottom-space--sm; }

// info
.p-card__content {
	width: nth($p-card-ratio, 2);
	float: right;
	clear: right;
	will-change: min-height;

	@include media('<desk-lg') {
		width: nth($p-card-ratio--lg, 2); }

	@include media('<tab-lg') {
		width: nth($p-card-ratio--sm, 2); } }

.p-card__content__holder {
	padding-right: 0;
	padding-bottom: $bottom-space--lg;
	transform: translate(0, 0) /* For browsers don't support translate3d. */;
	transform: translate3d(0, 0, 0);
	will-change: position, transform;

	@include media('<desk-lg') {
		padding-bottom: $bottom-space--md; }

	@include media('<tab-lg') {
		padding-bottom: $bottom-space--ml; } }

// price
.p-card__price {
	@include font-normal;
	line-height: 24px;
	color: $base-color;

	.val {
		display: inline-block;
		vertical-align: baseline;
		@include font-bold;
		font-size: 18px; }

	small {
		display: inline-block;
		vertical-align: baseline;
		font-size: 14px; }

	.old, .cur {
		position: relative;
		display: inline-block;
		vertical-align: baseline;
		margin-right: $grid-space; }

	.old {
		color: $text-color-light;

		&::after {
			@include pseudo();
			@include size(calc(100% + 4px), 0);
			left: -2px;
			bottom: 50%;
			margin-bottom: -1px;
			border-top: 1px solid $text-color-light; }

		.val {
			font-size: 14px; }

		small {
			font-size: 10px; } } }

// list of characteristics
.p-card__options__dl {
	margin-bottom: $p-card-elem-bottom-space;
	$dt-width: 184px;

	&:last-child {
		margin-bottom: 0; }

	.p-card__options__di {
		position: relative;
		padding: 10px 0;
		border-bottom: 1px dashed $text-color-light;
		@include clear;

		@include media('<mob-md') {
			@include padding-vertical(15px);
			@include font(1.6rem, round-float(24/16, 1)); } }

	.p-card__options__dt {
		float: left;
		width: $dt-width;
		color: $text-color-light;
		padding-right: 10px;

		@include media('<mob-md') {
			float: none;
			width: auto; } }

	.p-card__options__dd {
		margin-left: $dt-width;

		@include media('<mob-md') {
			margin-left: 0; } } }

// list of grid view parameters
.p-card__options__grid {
	@include clear;

	&, .#{$user-content} & {
		padding: 0 0 1px 1px;
		margin: 0 0 $p-card-elem-bottom-space;

		&:last-child {
			margin-bottom: 0; }

		li {
			padding: 8px;
			margin: 0 0 -1px -1px;

			&::before {
				content: none; } } }

	li {
		float: left;
		min-width: 60px;
		border: 1px solid $border-color;
		@include font(16px, 20px);
		text-align: center;

		@include media('<639px') {
			width: 12.5%; }

		@include media('<519px') {
			width: 14.28%; }

		@include media('<479px') {
			width: 16.67%; }

		@include media('<399px') {
			width: 20%; }

		@include media('<339px') {
			width: 25%; } } }

// list of colors
.p-card__color {
	@include clear;

	&, .#{$user-content} & {
		padding: 0 0 1px 1px;
		margin: 0 0 $p-card-elem-bottom-space;

		&:last-child {
			margin-bottom: 0; }

		li {
			padding: 8px 8px 8px 42px;
			margin: 0 0 -1px -1px;

			&::before {
				content: none; } } }

	li {
		position: relative;
		float: left;
		width: 33.33%;
		min-width: 170px;
		border: 1px solid $border-color;
		@include font($font-size, $line-height);
		letter-spacing: normal;
		@include truncate(none);
		cursor: default;

		@include media('<tab-lg') {
			width: 25%; }

		@include media('<tab-sm') {
			width: 33.3%; }

		@include media('<mob-lg') {
			width: 50%; }

		@include media('<mob-md') {
			width: 100%; } }

	.color-pic {
		position: absolute;
		left: 19px;
		top: 14px;
		margin: 0;
		z-index: 5;
		will-change: transform;
		transition: {
			property: transform;
			duration: 0.6s; } }

	.no-touchevents & li:hover {
		.color-pic {
			$scale: 30;
			transform: scale($scale);
			transform: scale3d($scale, $scale, $scale); } } }

// previous and next products
.p-card__btn {
	$pc-btn-size: 300px 430px; // width height
	$pc-btn-size--md: 180px 430px; // width height
	$pc-btn-size--sm: 120px 320px; // width height
	$animation-speed: $animation-speed;
	// ==================
	position: absolute;
	top: 130px;
	top: 50vh;

	//top: calc(50vh - #{$header-height + round($layout-spaces--hd*0.9)})
	@include size(nth($pc-btn-size, 1), nth($pc-btn-size, 2));
	//margin: -#{round($layout-spaces*0.9 + nth($pc-btn-size, 2)/2)} -#{round($layout-spaces + nth($pc-btn-size, 1)/2)} 0
	margin: -#{round($header-height + $layout-spaces*0.9/2 + nth($pc-btn-size, 2)/2)} -#{round($layout-spaces + nth($pc-btn-size, 1)/2)} 0;
	//text-decoration: none
	color: inherit;
	//z-index: 15
	will-change: color, opacity, visibility;
	transition: {
		property: color, opacity, visibility;
		duration: $animation-speed; }
	//timing-function: cubic-bezier(0, 0.71, 0.41, 1)

	@include media('<desk-hd') {
		margin: -#{round($header-height + $layout-spaces--hd*0.9/2 + nth($pc-btn-size, 2)/2)} -#{round($layout-spaces--hd + nth($pc-btn-size, 1)/2)} 0; }

	@include media('<desk-lg') {
		@include size(nth($pc-btn-size--md, 1), nth($pc-btn-size--md, 2));
		margin: -#{round($header-height--lg + $layout-spaces--lg*0.9/2 + nth($pc-btn-size--md, 2)/2)} -#{round($layout-spaces--lg + nth($pc-btn-size--md, 1)/2)} 0; }

	@include media('<desk-sm') {
		@include size(nth($pc-btn-size--sm, 1), nth($pc-btn-size--sm, 2));
		margin: -#{round($header-height--lg + $layout-spaces--sm*0.9/2 + nth($pc-btn-size--sm, 2)/2)} -#{round($layout-spaces--sm + nth($pc-btn-size--sm, 1)/2)} 0; }

	@include media('<tab-lg') {
		display: none; }

	// zoom on
	.zoom-on & {
		opacity: 0;
		visibility: hidden; }

	// title in button
	.p-card__btn__text {
		// vars ===========
		$space: 30px;
		$space--sm: 18px;
		$size: 240px 24px;
		//=================
		position: absolute;
		@include size(nth($size, 2));
		@include font(14px, nth($size, 2));
		top: 50%;
		left: $space;
		right: $space;
		margin: -#{round(nth($size, 2)/2)} 0 0;

		@include media('<desk-sm') {
			left: $space--sm;
			right: $space--sm; }

		span {
			display: block;
			@include size(nth($size, 1), nth($size, 2));
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%) rotate(-0.25turn);
			text-align: center;
			@include truncate(nth($size, 1)); } }

	//background-color: rgba(255, 182, 193, 0.5)

	// arrows in button
	.p-card__btn__arr {
		$space: 17px;
		$space--sm: 4px;
		//===============
		position: absolute;
		right: $space;
		left: $space;
		top: 50%;
		transition: {
			property: transform, opacity;
			duration: $animation-speed; }
		//timing-function: cubic-bezier(0, 0.71, 0.41, 1)

		@include media('<desk-sm') {
			right: $space--sm;
			left: $space--sm; }

		&::before, &::after {
			border-color: #e6e6e6; } }

	// images in button
	.p-card__btn__img {
		display: block;
		@include size(80%, 100%);
		@include push-auto;
		opacity: 0;
		transition: {
			property: transform, opacity;
			duration: $animation-speed; }
		//timing-function: cubic-bezier(0, 0.71, 0.41, 1)

		img {
			display: block;
			width: 100%;
			height: 100%;
			max-width: none;
			@include object-fit(none);
			@include object-fit(scale-down);

			.no-js & {
				width: auto;
				height: auto;
				max-width: 100%; } } }

	// prev button
	&.p-card__btn--prev {
		left: 0;
		margin-right: 0;

		.p-card__btn__text, .p-card__btn__arr {
			left: auto; }

		.p-card__btn__img {
			transform: translate(-20px, 0); } }

	// next button
	&.p-card__btn--next {
		right: 0;
		margin-left: 0;

		.p-card__btn__text, .p-card__btn__arr {
			right: auto; }

		.p-card__btn__img {
			transform: translate(20px, 0); } }

	// hover
	&:hover, &:not(.disabled):hover {

		color: $link-color;

		// arrows in button
		.p-card__btn__arr {
			opacity: 0;

			&::before, &::after {
				border-color: #e6e6e6; } }

		.p-card__btn__img {
			transform: translate(0, 0);
			opacity: 0.25; }

		// hover next btn
		&.p-card__btn--prev {

			// arrows in button
			.p-card__btn__arr {
				transform: translate(-20px, 0); } }

		// hover prev btn
		&.p-card__btn--next {

			// arrows in button
			.p-card__btn__arr {
				transform: translate(20px, 0); } } } }
// zoom on
.p-card__content, .p-card__visual, .p-card__gallery__item, .p-card__gallery__inner, .p-card__content__frame {
	will-change: margin, width, height, opacity, visibility, background-size;
	transition: {
		property: margin, width, height, opacity, visibility, background-size;
		duration: 0.4s;
		timing-function: cubic-bezier(0,.71,.41,1); } }

.p-card__gallery__item {
	@include media('>=tab-lg') {
		cursor: url(../../img/cursor-zoom-in.cur), zoom-in;
		user-select: none; } }

.zoom-on {
	@include media('>=tab-lg') {
		.p-card__content {
			//margin-right: -#{nth($p-card-ratio, 2)}
			opacity: 0;
			visibility: hidden; }

		.p-card__visual {
			width: 100%;
			margin-right: -#{nth($p-card-ratio, 2)}; }

		.p-card__gallery__item {
			cursor: url(../../img/cursor-zoom-out.cur), zoom-out; }
		//height: 100vh

		.p-card__gallery__inner {
			background-size: auto 100%; }

		.p-card__content__frame {
			margin-right: -200px;
			margin-left: 200px; } } }

